footer{
    background-color: #2A71B1;
    padding: 15px;
}

.footer{
    &__footerLogo{
        img{
            width: 70px;
        }
    }

    &__footerNumber{
        ul li {
            list-style-type: none;
            margin: 10px 0px 10px 0px;
            a{
                color: #fff;
                text-decoration: none;
                margin-left: 10px;
                font-size: 19px;
            }
            i{
                font-size: 17px;
                color: #F57914;
            }
        }
    }

    &__footerAdress{
        li{
            list-style-type: none;
            color: #fff;
            text-decoration: none;
            font-size: 19px;
            a{
                color: #fff;
                text-decoration: none;
            }
          
            i{
                font-size: 17px;
                color: #F57914;
                margin-right: 12px;

            }
        }
       
    }
}

// default
ul{
padding-left: 0  !important;
}

// default