.firstBlock{
   &__main{
    z-index: -1;
   }
&__img{
   
    img{
        width: 100%;
        border-radius: 10px;
        
    }
}

&__buttons{
    button{
        border: none;
        padding: 15px 35px;
        border-radius: 30px;
        background-color: #2A71B1 ;
        margin-bottom: 30px;
        
        a{
            color: #fff;
            font-size: 18px;
            font-weight: 600;
            text-decoration: none;
        }
        &:hover{
            transition: ease-in .2s;

            background: #F57914;
            
            a{
                color: #fff;
            }

        }
    }
}

    &__socialMedia{
        position: absolute;
        display: flex;
        flex-direction: column;
        right: 0;
        position: fixed;
        border: 3px dashed #F57914;
        padding: 10px;
        

        img{
            max-width: 60px;
            padding-top: 10px;
        }
    }


    &__main{
        position: relative;
    }

    
    &__inner{
   

  h1{
    color: #F57914;
    text-align: left;
    padding-bottom: 20px;
   }
        p{
         font-size: 21px;
         margin-bottom: 50px;
         color: #2A71B1;
         text-transform: uppercase;
         font-weight: 600;
        
        }
    }

   
}

.secondSection{
    &__boxesIcon{
        i{
            font-size: 32px;
            color: #F57914;
        }

    
    }

    &__texts{

        p{
            color: #2A71B1;
            font-size: 18px;
            font-family: Arial, Helvetica, sans-serif;
            font-weight: 500;
        }    }
        
}
// stilistika start sidenye

.secondSection {
    &__BusplacesBlock {
        z-index: -1;

        position: relative;
        img {
            width: 100%;
            border-radius: 10px;
            transition: 0.3s; // Плавный переход при изменении стиля
            max-height: 100%;
            min-height: 100%;
            height: 100%;
            
        }

        &:hover {
            img {
                filter: brightness(0.5); // Затемнение изображения при наведении
            }
            .secondSection__BusplacesPrice {
                display: block; // Показать цену при наведении
            }
            .secondSection__BusplacesPrice {
                p {
                    color: #F57914; // Белый цвет для цены при наведении
                }
            }
        }
    }

    &__BusplacesHead {
        hr {
        color: #F57914;
        opacity: 1;        }
    }

    &__BusplacesInner {
        p {
            color: #2A71B1;
            font-size: 18px;
        }
    }

    &__BusplacesPrice {
        display: none; 
        position: absolute;
        top: 25%;
        left: 50%;
        transform: translate(-50%, -50%); // Центрируем блок по центру
        transition: color 0.3s; // Плавный переход цвета

        p {
            font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
            font-size: 40px;
            font-weight: 500;
        }
    }
   
}

// stilistika end sidenye



