        max-width: 290px;
        border: 1px solid #2A71B1;

    }
}

.secondBlock__selecteded {
    display: flex;
    gap: 30px; // Отступ между радиокнопками
    margin: 20px 0;

    label {
        cursor: pointer;
        font-size: 18px;
        color: #fff; // Цвет текста
        position: relative;
        padding: 5px 8px; // Отступы вокруг текста
        border-radius: 5px; // Скругление углов
        background-color: #2A71B1; // Цвет фона
        transition: background-color 0.3s ease, transform 0.2s; // Плавный переход

        // Скрываем стандартные радиокнопки
        input[type="radio"] {
            appearance: none; // Убираем стандартный стиль радиокнопки
            display: none; // Скрываем стандартные радиокнопки

           
        }

        span {
            display: inline-block; // Для правильного отображения
            padding: 10px; // Отступ внутри
            border-radius: 5px; // Скругление углов
        }

        // Эффекты при наведении
        &:hover {
            background-color: #F57914; // Цвет фона при наведении
            transform: scale(1.05); // Увеличение при наведении
        }
    }
}

